import React,{ Component , useState } from 'react'
import Home from "./home";
import useLocalStorage from "./useLocalStorage";
import 'codemirror/lib/codemirror.css';
import 'codemirror/theme/material.css';
import 'codemirror/mode/xml/xml';
import 'codemirror/mode/javascript/javascript';
import 'codemirror/mode/css/css';
import { Controlled as ControlledEditor } from 'react-codemirror2';

{/* file explorer */}
class Files extends Component {
    constructor(props){
        super(props)
        this.state = { language:"",displayName:"",value:""}
        this.handleClick_html = this.handleClick_html.bind(this)
        this.handleClick_css = this.handleClick_css.bind(this)
        this.handleClick_js = this.handleClick_js.bind(this)
    }


    handleClick_html(){
    
        // Changing state
        this.setState({language: 'xlm',displayName:"Html"});
      
    }

    handleClick_css(){
    
        // Changing state
        this.setState({language: 'css',displayName: "CSS"});
       
    }

    handleClick_js(){
    
        // Changing state
        this.setState({language :'javascript',displayName: "JavaScript"});

    }
    render() { 
        return (
            <div>
                <br/>
                <div class="container">
                    <div class="row center">
                        <div class="col">
                            <button type="button" onClick={ this.handleClick_html } class="btn btn-outline-primary center">index.html</button>
                        </div>
                        <div class="col">
                            <button type="button" onClick={ this.handleClick_css } class="btn btn-outline-success center">index.css</button>
                        </div>
                        <div class="col">
                            <button type="button" onClick={ this.handleClick_js } class="btn btn-outline-danger center">index.js</button>
                        </div>
                    </div>
                </div>
                <br/>
                <div className="editor-title">
                    {this.state.displayName}
                </div>
                <ControlledEditor
                    value= {this.state.value}
                    className="code-mirror-wrapper"
                    options={{
                    lineWrapping: true,
                    lint: true,
                    mode: this.state.language,
                    theme: 'material',
                    lineNumbers: true
                    }}
                />
            </div>
        );
    }
}
 
export default Files;

